.layerfordark {
    position: fixed;
    max-width: 45em;
    min-height: 35em;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0.6;
    transition: all 0.7s;
}

.mypagelayerfordark {
    position: fixed;
    max-width: 80em;
    margin-left: auto;
    margin-right: auto;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    background-color: black;
    opacity: 0.6;
    transition: all 0.7s;
    z-index: 1;
}

@keyframes godark {
    0% {opacity: 0;}
    100% {opacity: 0.6;}
}

@keyframes golight {
    0% {display: "block"; opacity: 0.6;}
    100% {display: "block"; opacity: 0;}
}

