.voiceCircle {
    position : fixed;
    top: 15%;
    left : 0%;
    right: 0%;
    margin-left: auto;
    margin-right: auto;
    width : 9.5em;
    height : 9.5em;
    border-radius: 100%;
    border: dotted 5px;
    border-color: rgba(255,255,255,0.3);
    z-index: 0;
    animation-name: spinCircle;
    animation-duration:20s;
    animation-iteration-count:infinite;
    animation-timing-function:linear;
} 

@keyframes spinCircle{
    from {
        transform: rotate(0);
    }
    to {
        transform : rotate(360deg);
    }
}

.QuestioningWrapper{
    position:absolute;
    top: 0;
